<template>
  <div id="detail">
    <!-- 顶部导航栏 -->
    <detail-nav-bar class="nav-bar" @titleClick="titleClick" ref="nav" />
    <!-- 滚动部分 -->
    <scroll class="conten" :probe-type="3" @scroll="contentScroll" ref="scroll">
      <!-- 轮播图部分 -->
      <detail-swiper :top-images="topImages" />
      <!-- 商品信息 -->
      <detail-base-info :goods="goods" />
      <!-- 商家信息 -->
      <detail-shop-info :shop="shop" />
      <!-- 商品展示部分 -->
      <detail-goods-info :detail-info="detailInfo" @detailImageLoad="detailImageLoad" />
      <!-- 商品详情部分 -->
      <detail-param-info ref="param" :paramInfo="paramInfo" />
      <!-- 评论信息 -->
      <detail-comment-info ref="comment" :comment-info="commentInfo" />
      <!-- 商品推荐部分 -->
      <goods-list ref="recommend" :goods="recommend" />
    </scroll>
    <!-- 底部导航栏 -->
    <detail-bottom-bar @addToCart="addToCart" />
    <!-- 回到顶部 -->
    <back-top @click.native="backClick" v-show="isShowBackTop" />
    <!-- 组件方式导入Toast -->
    <!-- <toast :message="message" :show="show" /> -->
  </div>
</template>

<script>
//子组件
import DetailNavBar from "./childComps/DetailNavBar";
import DetailSwiper from "./childComps/DetailSwiper";
import DetailBaseInfo from "./childComps/DetailBaseInfo";
import DetailShopInfo from "./childComps/DetailShopInfo";
import DetailGoodsInfo from "./childComps/DetailGoodsInfo";
import DetailParamInfo from "./childComps/DetailParamInfo";
import DetailCommentInfo from "./childComps/DetailCommentInfo";
import DetailBottomBar from "./childComps/DetailBottomBar";

//公共组件
import Scroll from "components/common/scroll/Scroll";
import GoodsList from "components/content/goods/GoodsList";
// import Toast from "components/common/toast/Toast";

//vuex
import { mapActions } from "vuex";

// 公共方法
import { itemListenerMixin, backTopMixin } from "common/mixin";
import { debounce } from "common/utils";

// 获取数据
import {
  getDetail,
  Goods,
  Shop,
  GoodsParam,
  getRecommend
} from "network/detail";

export default {
  name: "Detail",
  mixins: [itemListenerMixin, backTopMixin],

  data() {
    return {
      iid: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      recommend: [],
      themeTopYs: [],
      getThemeTopY: null,
      currentIndex: 0
      // show: false,
      // message: ""
    };
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    DetailBottomBar,

    getRecommend,
    GoodsList,
    // Toast,

    Scroll
  },
  methods: {
    //vuex导入计算属性
    //数组方式
    ...mapActions(["addCart"]),

    //对象方式
    // ...mapActions({
    //   add:'addCart'
    // }),

    //重新计算可滚动的高度
    detailImageLoad() {
      this.refresh();
      this.getThemeTopY();
    },
    //点击跳转到对应的位置
    titleClick(index) {
      // console.log(index);
      this.$refs.scroll.scrollTo(0, -this.themeTopYs[index], 1000);
    },
    //监听滚动
    contentScroll(position) {
      const positionY = -position.y;
      //显示回到顶部按钮
      this.isShowBackTop = -position.y >= 1000;
      let length = this.themeTopYs.length;
      //判断显示导航到哪部分
      for (let i = 0; i < length; i++) {
        if (
          this.currentIndex !== i &&
          ((i < length - 1 &&
            positionY >= this.themeTopYs[i] &&
            positionY < this.themeTopYs[i + 1]) ||
            (i === length - 1 && positionY >= this.themeTopYs[i]))
        ) {
          this.currentIndex = i;
          //和顶部导航的currentIndex一一对应
          this.$refs.nav.currentIndex = this.currentIndex;
        }
      }
    },
    addToCart() {
      //获取添加到购物车的数据
      const product = {};
      product.image = this.topImages[0];
      product.title = this.goods.title;
      product.desc = this.goods.desc;
      product.price = this.goods.realPrice;
      product.iid = this.iid;

      //通过导入vuex的方式将计算属性引入进来
      //返回promise对象让里面的方法属性先加载完成
      this.addCart(product).then(res => {
        //组件方式导入
        // console.log(res);
        // this.show = true;
        // this.message = res;
        // setTimeout(() => {
        //   this.show = false;
        //   this.message = "";
        // }, 2000);
        //插件方式导入
        this.$toast.show(res);
      });
      //将商品放到购物车里面
      // this.$store.dispatch("addCart", product).then(res => {
      //   console.log(res);
      // });
    }
  },
  created() {
    //保存iid
    this.iid = this.$route.params.iid;
    //获取数据
    getDetail(this.iid).then(res => {
      const data = res.result;
      //轮播图数据
      this.topImages = data.itemInfo.topImages;
      //console.log(this.topImages);

      //获取商品信息
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      //获取店铺信息
      this.shop = new Shop(data.shopInfo);
      //获取展示图片信息
      this.detailInfo = data.detailInfo;

      //获取店铺商品信息
      this.paramInfo = new GoodsParam(
        data.itemParams.info,
        data.itemParams.rule
      );
      //评论信息
      if (data.rate.cRate !== 0) {
        this.commentInfo = data.rate.list[0];
      }

      //dom渲染完成但是图片还没加载完成会有误差
      //$nextTick等到dom渲染完成回调内部函数
      // this.$nextTick(() => {
      //   this.themeTopYs = [];
      //   this.themeTopYs.push(0);
      //   this.themeTopYs.push(this.$refs.param.$el.offsetTop);
      //   this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
      //   this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      // });
    });
    //获取评论信息
    getRecommend().then(res => {
      this.recommend = res.data.list;
    });

    //进行防抖防止多次加载
    this.getThemeTopY = debounce(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(this.$refs.param.$el.offsetTop);
      this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
      this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      // console.log(this.themeTopYs);
    }, 200);
  },
  updated() {
    //可以在这进行获取offsettop值
    //比较方便
    //  this.themeTopYs = [];
    //   this.themeTopYs.push(0);
    //   this.themeTopYs.push(this.$refs.param.$el.offsetTop);
    //   this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
    //   this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
    // console.log(this.themeTopYs);
  },
  mounted() {},
  destroyed() {
    //事件总线监听图片加载问题
    this.$bus.$off("itemImageLoad", this.itemImgListener);
  }
};
</script>

<style scoped>
#detail {
  position: relative;
  background-color: #fff;
  z-index: 9;
  height: 100vh;
}
.nav-bar {
  position: relative;
  z-index: 9;
  background-color: #fff;
}
.conten {
  height: calc(100% - 93px);
  overflow: hidden;
}
</style>